<template>
    <div class="theme-container">
      <!--bannar -->
      <Header></Header>
      <!-- 导航栏 -->
      <Navbar></Navbar>
      <div class="header">
          <h2>精美手账制作模板</h2>
          <h5>点击任意手账即可开始设计</h5>
      </div>
      <!-- 手账主题部分 -->
      <div class="demo-box">
        <div class="block" v-for="(item, index) in themeList" :key="index" >
          <el-image class="pc"
           :src="item.url"
            style="width:12vw"
            @click="jumptoDesign(item.url)"
            >
          </el-image>
           <div class="demonstration">
             {{item.value}}
             <img :src="icon"
             @click="collectionNote(item.url)"
              width="12vw">
             </div>
        </div>
      </div>
      <br>
      <Footer></Footer>
    </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import Header from '@/components/home/header-roll.vue'
import Navbar from '@/components/home/navbar.vue'
import Footer from '@/components/home/footer.vue'
import themeDemo1 from '@/assets/theme-demo/theme-demo1.png'
import themeDemo2 from '@/assets/theme-demo/theme-demo2.png'
import themeDemo3 from '@/assets/theme-demo/theme-demo3.png'
import themeDemo4 from '@/assets/theme-demo/theme-demo4.png'
import themeDemo5 from '@/assets/theme-demo/theme-demo5.png'
import themeDemo6 from '@/assets/theme-demo/theme-demo6.png'
import themeDemo7 from '@/assets/theme-demo/theme-demo7.png'
import themeDemo8 from '@/assets/theme-demo/theme-demo8.png'
import themeDemo9 from '@/assets/theme-demo/theme-demo9.png'
import themeDemo10 from '@/assets/theme-demo/theme-demo10.png'
import bg1 from '@/assets/background/bg-1.png'
import iconCollectAfter from '@/assets/icon/icon-collect-after.png'
export default {
  components: {
    Header,
    Navbar,
    Footer,
    bg1
  },
  data () {
    return {
      // 灰色星星
      icon: iconCollectAfter,
      // 账本数据列表
      themeList: [
        { url: themeDemo1,
          value: '今日计划简约卡通手账'
        },
        { url: themeDemo2,
          value: '假期安排手账'
        },
        { url: themeDemo3,
          value: '卡通日历简约风手账'
        },
        { url: themeDemo4,
          value: '创意文艺手绘风手账'
        },
        { url: themeDemo5,
          value: '美食插画手账'
        },
        { url: themeDemo6,
          value: '清新可爱手抓饼手账'
        },
        { url: themeDemo7,
          value: '周末假期履行手账'
        },
        { url: themeDemo8,
          value: '日历工作手账'
        },
        { url: themeDemo9,
          value: '美食记录手账'
        },
        { url: themeDemo10,
          value: '日记可爱手账'
        }

      ],
      activeIndex: '1'
    }
  },
  computed: {
    ...mapState(['collect'])
  },
  methods: {
    ...mapMutations(['addToCollect']),
    // 跳转到design页面 携带图片url参数
    jumptoDesign (imageUrl) {
      this.$router.push({
        name: 'Design',
        params: {
          url: imageUrl
        }
      })
    },
    // 收藏到本地
    collectionNote (url) {
      this.addToCollect(url)
    }
  },
  created () {
    console.log(this.collect)
  }
}
</script>

<style lang="less" scoped>
.theme-container {
  background-color: #F5F7F9;
    .header {
      height: 12vh;
      color:rgb(34, 34, 34);
      text-align: center;
      line-height: 4vh;
      h1 {
        font-size: 2rem;
      }
      h5 {
        font-family: "宋体";
        font-weight: normal;
        font-size: 0.8rem;
      }
    }
    .demo-box {
      display: flex;
      margin: 0 10vw;
      padding-bottom: 2vh;
      background-color: #FFFFFF;
      flex-wrap: wrap;
      .block {
        text-align: center;
        padding: 5px;
        .demonstration {
          font-size: 0.8rem;
          color: rgb(102, 91, 91);
        }
        .pc {
          margin-bottom: 1vh;
          border-radius: 2%;
        }
        margin: 2vw 0 0 2vw;
      }
    }
}
</style>



