<template>
  <div>
    <template>
  <el-carousel :interval="4000" type="card" height="200px">
    <el-carousel-item v-for="item in banerList" :key="item">
      <h3 class="medium">
        <img :src="item" width="100%">
      </h3>
    </el-carousel-item>
  </el-carousel>
 </template>
  </div>
</template>
<script>
import banner1 from '@/assets/background/baner1.png'
import banner2 from '@/assets/background/baner2.png'
import banner3 from '@/assets/background/baner3.png'
import banner4 from '@/assets/background/baner4.png'
import banner5 from '@/assets/background/baner5.png'
export default {
  data () {
    return {
      banerList: [
        banner1,
        banner2,
        banner3,
        banner4,
        banner5
      ]
    }
  },
  created () {},
  components: {
    banner1
  },
  computed: {},
  methods: {}
}
</script>
<style lang="less" scoped>
body {
  margin: 0;
  padding: 0;
}

.layout-logo {
  // width: 100px;
  // height: 30px;
  // background-image: './assets/WechatIMG886.png';
  // border-radius: 3px;
  float: left;
  position: absolute;
  top: 0.8vh;
  left: 1.4vw;
  img {
    height: 6.1vh;
    width: 3.47vw;
  }
}
.layout-nav {
  width: 29.17vw;
  margin: 0 auto;
  margin-right: 1.39vw;
}
.ivu-menu-dark {
  background: #faac58;
}
.ivu-layout-header {
  background: #faac58;
  padding: 0 50 px;
  height: 64 px;
  line-height: 64px;
}

 .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

</style>



